import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CFormInput,
  CFormLabel,
  CCardFooter,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
  CFormCheck,
  CFormSelect,
  CMultiSelect,
  CFormFloating,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import './step_1.css';
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import {
  faTimesCircle,
  faCheckCircle,
  faArrowRight,
  faSave,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Formik, Form, Field, FieldArray, ErrorMessage } from "formik";
import * as Yup from "yup";
import Loader from "src/loader/Loader";
import EVFormikControl from "src/ev_formik_controls/EVFormikControl";
import EDU_AXIOS from "src/helpers/edu_axios";
import notify from "src/helpers/notify";
import { Link } from "react-router-dom";

const time_table = (props) => {
  let school_id = props.match.params.school_id;
  let [visible, setVisible] = useState(false);
  let [groups, setGroups] = useState([]);
  let [p_per_day, setPPerDay] = useState(0);
  let [periodsperday, setPeriodsPerDay] = useState(10);
  let [academicyear, setAcademicYear] = useState("");
  let maxperiods_to_be_displayed=[];
  // open cancle modal box
  let openModalBox = () => {
    setTimeout(() => setVisible(true), 0);
  };

  // close cancle modal box
  let closeModalBox = () => {
    setTimeout(() => setVisible(false), 0);
  };

  // no. of periods
  let periods = [];
  for (let i = 1; i <= 10; i++) {
    periods.push(i);
  }

  // no. of periods
  let cl = [];
  for (let i = 1; i <= 10; i++) {
    cl.push(i);
  }

  // length of periods
  let p_length = [];
  for (let i = 30; i <= 180; ) {
    let val = {
      text: i + " " + "Mins",
      value: i,
    };
    p_length.push(val);
    i = i + 5;
  }

  // gap b/w periods
  let p_gap = [];
  for (let i = 0; i <= 30; ) {
    p_gap.push(i);
    i = i + 5;
  }

  // weekends
  let weekends = [
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
    "Sunday",
    "Monday-Tuesday",
    "Tuseday-Wednesday",
    "Wednesday-Thursday",
    "Thursday-Friday",
    "Friday-Saturday",
    "Saturday-Sunday",
  ];

  let day_periods = [
    { text: "Period 1", value: "1" },
    { text: "Period 2", value: "2" },
    { text: "Period 3", value: "3" },
    { text: "Period 4", value: "4" },
    { text: "Period 5", value: "5" },
    { text: "Period 6", value: "6" },
    { text: "Period 7", value: "7" },
    { text: "Period 8", value: "8" },
    { text: "Period 9", value: "9" },
    { text: "Period 10", value: "10" },
  ];

  let weekdays = [
    { text: "Mon", value: "Mon" },
    { text: "Tue", value: "Tue" },
    { text: "Wed", value: "Wed" },
    { text: "Thu", value: "Thu" },
    { text: "Fri", value: "Fri" },
    { text: "Sat", value: "Sat" },
    { text: "Sun", value: "Sun", disabled: true },
  ];
  useEffect(() => {
    EDU_AXIOS.get(`${process.env.REACT_APP_API_PATH}/cm/get-class/${school_id}`)
      .then((response) => {
        let grp = [];
        if (response.data.data != "") {
          for (let i = 0; i < response.data.data.groups.length; i++) {
            grp.push(response.data.data.groups[i].group_name);
          }
        }
        setGroups(grp);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);
  let session = `${new Date().getFullYear()}/${new Date().getFullYear() + 1}`;
  // formik params
  let initialValues = {
    session: session,
    periods_per_day: "",
    start_time: "",
    p_length: "",
    p_gap: "",
    weekends: "",
    lunch_after: "",
    lunch_duration: "",
    weekdays: [],
    breaks: [],
    monday: [],
    tuesday: [],
    wednesday: [],
    thursday: [],
    friday: [],
    saturday: [],
  };
  let onChangeGroups = (e, field, values, setValues) => {
    // update dynamic form
    const breaks = [...values.breaks];
    const numberOfGroups = e.target.value || 0;
    const previousNumber = parseInt(field.value || "0");
    if (previousNumber < numberOfGroups) {
      for (let i = previousNumber; i < numberOfGroups; i++) {
        breaks.push({ break_after: "", break_duration: "" });
      }
    } else {
      for (let i = previousNumber; i >= numberOfGroups; i--) {
        breaks.splice(i, 1);
      }
    }
    setValues({ ...values, breaks });

    // call formik onChange method
    field.onChange(e);
  };

  // validation rules of the form fields
  const validationSchema = Yup.object().shape({
    session: Yup.string().required("Session is required"),
    group_name: Yup.string().required("Group name is required"),
    periods_per_day: Yup.string().required("Periods is required"),
    start_time: Yup.string().required("Start time is required"),
    p_length: Yup.string().required("Period length is required"),
    p_gap: Yup.string().required("Gap b/w periods is required"),
    weekends: Yup.string().required("weekends is required"),
    lunch_after: Yup.string().required("Lunch after period is required"),
    lunch_duration: Yup.string().required("Lunch duration is required"),
    weekdays: Yup.array().min(5).of(Yup.string().trim().required()),
    breaks: Yup.array().of(
      Yup.object().shape({
        break_after: Yup.string().required("Break after period is required"),
        break_duration: Yup.string().required("Break duration is required"),
      })
    ),
  });
  // submit form
  const onSubmit = async (values, { resetForm }) => {
    await EDU_AXIOS.post(
      process.env.REACT_APP_API_PATH + "/ttm/initialize-time-table",
      {
        school_id: school_id,
        acadmic_year: values.session,
        group: values.group_name,
        prd_per_day: values.periods_per_day,
        start_time: values.start_time,
        lunch_after: values.lunch_after,
        lunch_duration: values.lunch_duration,
        period_length: values.p_length,
        gap: values.p_gap,
        weekends: values.weekends,
        breaks: values.breaks,
        working_days: values.weekdays,
        monday: values.monday,
        tuesday: values.tuesday,
        wednesday: values.wednesday,
        thursday: values.thursday,
        friday: values.friday,
        saturday: values.saturday,
      }
    )
      .then(async (res) => {
        resetForm({ values: "" });
        notify("success", res.data.message);
      })
      .catch((err) => {
        notify("error", err);
      });
  };
  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          <CRow>
            <CCol md="8" lg="4" xl="8">
              <h5>Time Table [ Step 1 ]</h5>
            </CCol>
          </CRow>
          <p className="text-muted">Create Time Table</p>
          <CRow className="mb-3">
  <CFormLabel htmlFor="staticEmail" className="col-sm-2 col-form-label required-field">Academic Year</CFormLabel>
  <CCol sm={5}>
    <CFormInput type="text" id="staticEmail" defaultValue={academicyear} onChange={(e)=>setAcademicYear(e.target.value)} />
  </CCol>
</CRow>
<CRow className="mb-3">
  <CFormLabel htmlFor="inputPassword" className="col-sm-2 col-form-label">Groups</CFormLabel>
  <CCol sm={5}>
  <CFormSelect size="sm" className="mb-3" aria-label="Small select example">
  <option>Open this select menu</option>
  <option value="1">One</option>
  <option value="2">Two</option>
  <option value="3">Three</option>
</CFormSelect>
    </CCol>
</CRow>
<CRow className="mb-3">
  <CFormLabel htmlFor="inputPassword" className="col-sm-2 col-form-label">Periods Per Day (Max)</CFormLabel>
  <CCol sm={5}>
  <CFormSelect size="sm" onChange={(event)=>setPeriodsPerDay(event.target.value)} className="mb-3" aria-label="Small select example">
  <option>Open this select menu</option>
  <option value="1">One</option>
  <option value="2">Two</option>
  <option value="3">Three</option>
  <option value="4">Four</option>
  <option value="5">Five</option>
  <option value="6">Six</option>
  <option value="7">Seven</option>
  <option value="8">Eight</option>
  <option value="9">Nine</option>
  <option value="10">Ten</option>
</CFormSelect>
    </CCol>
</CRow>

<CRow className="mb-3">
  <CFormLabel htmlFor="starttime" className="col-sm-2 col-form-label">Start Time</CFormLabel>
  <CCol sm={5}>
    <CFormInput type="time" id="starttime"   />
  </CCol>
</CRow>


<CRow className="mb-3">
  <CFormLabel htmlFor="periodlength" className="col-sm-2 col-form-label">Priod Length</CFormLabel>
  <CCol sm={5}>
    <CFormInput type="text" id="periodlength"   />
  </CCol>
</CRow>


<CRow className="mb-3">
  <CFormLabel htmlFor="gapbetweenperiods" className="col-sm-2 col-form-label">Gap between periods</CFormLabel>
  <CCol sm={5}>
    <CFormInput type="text" id="gapbetweenperiods"   />
  </CCol>
</CRow>


<CRow className="mb-3">
  <CFormLabel htmlFor="lunch" className="col-sm-2 col-form-label">Lunch</CFormLabel>
  <CCol sm={3}>
  After Period  <CFormInput type="text" id="lunch"   />
  </CCol>
  <CCol sm={3}>
   Duration <CFormInput type="text" id="lunch"   />
  </CCol>
  <CCol sm={3}>
 Add /Remove breaks <CFormInput type="button"  defaultValue="Add breaks" />
  </CCol>
 
</CRow>


<CRow className="mb-4">
  <CFormLabel htmlFor="workingdaysperweek" className="col-sm-3 col-mb-3 col-form-label">Working Days Per Week</CFormLabel>
  <CCol sm={1} id="workingdaysperweek">
  <CFormCheck id="flexCheckDefault" label="Monday"/>
  </CCol>
  &nbsp;
  <CCol sm={1}>
  <CFormCheck id="flexCheckDefault" label="Tuesday"/>
  </CCol>
  &nbsp;
  <CCol sm={1}>
  <CFormCheck id="flexCheckDefault" label="Wednesday"/>
  </CCol>
  &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;&nbsp;
  <CCol sm={1}>
  <CFormCheck id="flexCheckDefault" label="Thursday"/>
  </CCol>&nbsp;
  <CCol sm={1}>
  <CFormCheck id="flexCheckDefault" label="Friday"/>
  </CCol>&nbsp;
  <CCol sm={1}>
  <CFormCheck id="flexCheckDefault" label="Saturday"/>
  </CCol>&nbsp;
  <CCol sm={1}>
  <CFormCheck id="flexCheckDefault" label="Sunday"/>
  </CCol>
</CRow>

{
  day_periods.map((item,index)=>{
     index<=periodsperday-1?maxperiods_to_be_displayed.push({text:day_periods[index].text,value:day_periods[index].value}):''
  })
}
{
  maxperiods_to_be_displayed.map(item=>{
    return item.text
  })
}
<table border="1">
  <tr><td>Periods-&gt;<br></br>Days-&#8595;</td>
  {
    maxperiods_to_be_displayed.map((item,index)=>{
     return <td>{item.text}</td>
    })
  }
  
 
  
  </tr>
  {
    weekdays.map((item,i)=>{
      return <tr>
        <td>{item.value}</td>
       {
          maxperiods_to_be_displayed.map((item_periods,j)=>{
            return <td>
              {/* {item_periods.text} */}
              <input  text-align="center" type="checkbox"></input></td>
          })
          }
      </tr>
    })
  }
</table>


        </CCardBody>
      </CCard>
    </CCardGroup>
  );
};

export default time_table;
